.hidden {
  display: none;
}

.cat-info {
  border: 2px solid gray;
  gap: 30px;
  display: flex;
}

.cat-none {
  display: none;
}

h2 {
  color: brown;
}

.error {
  text-align: center;
  color: red;
  margin: 20px;
}

.error-none {
  display: none;
}

.loader {
  width: 70px;
  height: 70px;
  box-sizing: border-box;
  border: 5px solid tomato;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: fixed;
  bottom: 50%;
  left: 50%;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

